.header {
  background: #1879be;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 1;
  width: 69.9vw;
  height: 10vh;
  color: white;
  padding-right: 1vw;
  img {
    width: 8%;
    margin-left: 2%; }
  a {
    color: white;
    text-decoration: none;
    font-size: 1.5vw; } }

.loginLogout {
  text-align: center;
  button {
    background: transparent;
    border: none;
    border-bottom: 1px solid black;
    font-size: 1.2vw; } }
